export const vueFile = (formCols, rules, formData) => {
    let file = `<template>
                    <div class="vueFile">
                    <zx-form
                        ref="mForm"
                        :formData="formData"
                        :formCols="formCols"
                        :rules="rules">
                    </zx-form>
                  </div>
                 </template>

                <script type="text/ecmascript-6">
                export default {
                  name: "vueFile",
                  data() {
                    return {
                      formData: ${JSON.stringify(formData)},
                      formCols: ${JSON.stringify(formCols)},
                      rules:${JSON.stringify(rules)},
                    }
                  },
                }
                </script>`
    return file
}
